<template>
  <div>
    <v-container fluid>
      <v-card tile outlined class="rounded-lg">
        <v-toolbar flat dense color="dark">
          <v-icon left>mdi-notebook</v-icon>
          <v-toolbar-title>Log</v-toolbar-title>
          <v-spacer></v-spacer>
        </v-toolbar>
        <v-divider></v-divider>
        <v-flex style="height:550px;overflow-y:auto;overflow-x:hidden;" >
          <v-row dense class="pa-3">
            <v-col cols="12" md="9">
              <v-skeleton-loader
                v-for="n in 3"
                :key="n"
                height="22"
                type="image"
                class="mx-3 mt-3 rounded-xl"
                v-show="loading == true"
              ></v-skeleton-loader>
            </v-col>
            <!-- <v-col v-for="log in Log" :key="log.Jam+log.Job" cols="12" sm="12" md="12">
              <label class="text-justify" v-show="loading == false">
                Jam : {{log.Jam}} || 
                Tanggal : {{log.Tanggal}} ||
                {{log.KodeUser}} {{log.Job}}
              </label>
            </v-col> -->

            <v-col cols="12" sm="12" md="12" v-show="loading == false">
              <v-simple-table dense>
                <template v-slot:default>
                  <thead>
                    <tr>
                      <th class="text-left" width=100><v-icon left>mdi-clock-outline</v-icon>Jam</th>
                      <th class="text-left" width=120><v-icon left>mdi-calendar-outline</v-icon>Tanggal</th>
                      <th class="text-left" width=170><v-icon left>mdi-account-box-outline</v-icon>KodeUser</th>
                      <th class="text-left" width=300><v-icon left>mdi-history</v-icon>Aktivitas</th>
                    </tr>
                  </thead>
                  <tbody>
                    <tr
                      v-for="log in Log"
                      :key="log.Jam+log.Job"
                    >
                      <td>{{log.Jam}}</td>
                      <td>{{log.Tanggal}}</td>
                      <td>{{log.KodeUser}}</td>
                      <td>{{log.Job}}</td>
                    </tr>
                  </tbody>
                </template>
              </v-simple-table>
            </v-col>
          </v-row>
        </v-flex>
      </v-card>
    </v-container>
  </div>
</template>

<script>
import api from "@/services/http";
  export default {
    data: () => ({
      loading:true,
      DialogUserMenu: false,
      editedIndex: -1,
      token:null,
      MenuTemp:[],
      ParentTemp:[],
      ObjectTemp:[],
      ParentSelect:null,
      JabatanSelect:null,
      resMenu:[],
      defaultItem: {
        KodeMenu: "",
        Nama: "",
        Object: "",
        UserMenu: "",
        Visible: "",
        Icon: ""
        
      },
      editedItem: {
        KodeMenu: "",
        Nama: "",
        Object: "",
        UserMenu: "",
        Visible: "",
        Icon: ""
        
      },
      Log:[],
      Beranda: [
        // { Nama: 'Beranda'},
      ],

      Master: [
        // {
        //   items: [
        //     { Nama: 'Karyawan' },
        //     { Nama: 'UserMenu' }

        //   ],
        //   Nama: 'Master',
        // },
      ],
    }),

    mounted(){
      this.token = localStorage.getItem('token')
      this.getLog()
      this.ee()
    },

    computed: {
      formTitleUserMenu () {
        return this.editedIndex === -1 ? 'Tambah User Menu' : 'Ubah User Menu'
      },
    },

    watch: {
      DialogUserMenu(val) {
        val || this.KeluarDialogUserMenu();
        if (this.DialogUserMenu == true) {
          this.ObjectTemp = ['List','Link']
          api.get('menuselected/'+this.JabatanSelect+'?token='+this.token).then(res=>{
            this.MenuTemp = res.data
            var parent = res.data.Menu.filter( function(item){return (item.Parent == null && item.Object != "Link");} );
            this.ParentTemp = parent
          })
        }
      },
      ParentSelect(){
        if (this.ParentSelect == null || this.ParentSelect.length == 0) {
          //do nothing
          this.ObjectTemp = [...new Array]
          this.ObjectTemp = ['List','Link']
        }else if(this.ParentSelect.Object == "List") {
          this.ObjectTemp = ['Page']
        }else{
          //do nothing
        }
      }
    },

    DialogUserMenu (val) {
      val || this.KeluarDialogUserMenu()
    },

    methods: {
      getLog:function(){
        api.get('log').then(
        res=>{
          this.loading = false
          let a = JSON.stringify(res.data)
          this.Log = [...new Array]
          this.Log = JSON.parse(a)
        //   this.Log = a
        })
        // api.get('/log').then(
		// 		res => {
        //   console.log(res.data)
        //   this.Log = res.data
		// 		},
		// 		err => {
		// 			console.log(err);
		// 		}
		// 	)
      },
      ee(){
        // let url = "http://192.168.1.76:8000/log"
        // fetch(url)
        // .then((resp) => resp.json())
      },

      KeluarDialogUserMenu () {
        this.DialogUserMenu = false
        this.$nextTick(() => {
          this.editedItem = Object.assign({}, this.defaultItem)
          this.ParentSelect = null
          this.editedIndex = -1
        })
      },

      Reset() {
        this.$nextTick(() => {
          this.editedItem = Object.assign({}, this.defaultItem)
        })
      },
    },
  }
</script>

<style>
/* table th + th { border-left:1px solid #dddddd; }
table td + td { border-left:1px solid #dddddd; } */
</style>